.chartArchive {
  &__navbar {
    display: flex;
    height: 40px;
    align-items: center;
    justify-content: space-between;
  }
  &__title {
    font-size: 22px;
    line-height: 125.3%;
    color: #192434;
  }
  &__filter {
    display: flex;
    justify-content: space-between;
    width: 88px;
  }
  &__content {
    height: 100%;
    display: flex; 
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-around;
    row-gap: 20px;
  }
  &__info {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__date {
    font-size: 13px;
    line-height: 125.3%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: rgba(28, 46, 69, 0.6);
  }
  &__money {
    font-weight: bold;
    font-size: 32px;
    line-height: 132.6%;
    color: rgba(24, 39, 58, 0.94);
    margin-top: 16px;
    &-statistic {
      margin-left: auto;
      margin-top: 0;
      font-size: 14px;
    }
    &-percent {
      color: rgba(28, 48, 74, 0.5);
      margin-left: 4px;
    }
  }
  &__percent {
    font-weight: 600;
    font-size: 22px;
    line-height: 132.6%;
    text-align: center;
    margin-left: 20px;
    color: rgba(28, 48, 74, 0.5);
  }
  &__subtitle {
    margin-top: 8px;
    font-size: 9.97472px;
    line-height: 125.3%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: rgba(27, 43, 65, 0.72);
  }
  &__statistics {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    row-gap: 8px;
  }
  &__statistic {
    display: flex;
    align-items: center;
    column-gap: 10px;
  }
  &__statisticTitle {
    font-size: 14px;
    line-height: 161.1%;
    color: rgba(24, 39, 58, 0.94);
  }
  .filter {
    &__icon {
      border-radius: 4px;
      width: 40px;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      &-category {
        background: rgba(25, 59, 103, 0.05);
      }
      &-date {
        background: rgba(28, 52, 84, 0.26);
      }
    }
  }
}